import React from 'react';
import MaterialTable from 'material-table';
import Toolbar from '@material-ui/core/Toolbar';
import Grid from '@material-ui/core/Grid';

import './font_icons.css';

function ExpandableTable(props) {


  return (

    <MaterialTable

      columns={[

        { title: 'Report Name', field: 'reportName', headerStyle: {
					  fontWeight: 'bold',
					  
					} },

        { title: 'Owner Name', field: 'owner', headerStyle: {
					  fontWeight: 'bold',
					  
					} },
        
        

        

      ]}
      


      data={props.table_data}
      

      

      
      options={{
          headerStyle: {
            backgroundColor: '#130f40',
            color: '#FFF'
          }
          }
          }
          
      

      title= {props.table_data.length.toString().concat(" Matching Results")}

      detailPanel={rowData => {

        return (
				<Toolbar>
				
				<Grid container alignItems="center" justify="center">
				
				<Grid item>
				
        <MaterialTable
		  
				options={{
				  search: false,
				  paging: false
				}}

				  columns={[

					{ title: 'Users', field: 'userName' ,
					
					headerStyle: {
					  fontWeight: 'bold',
					  backgroundColor: 'black',
					  color: 'white'
					}
					
					},

					
				  ]}

				  data={[

					{ userName:'Brett Lee'},

					{ userName:  'Gilchrist'},
					
					{ userName:  'Ricky Ponting'},
					
					{ userName:  'Andrew Symonds'},

				  ]}

				  title=""
	  
	  />
	  
	  </Grid>
	  
	  </Grid>
	  </Toolbar>
	  

        )

      }}

      onRowClick={(event, rowData, togglePanel) => togglePanel()}

    />

  )

}

export default  ExpandableTable;
